import React, { useEffect, useState } from 'react';
import { Button, Container, Nav, Navbar, Modal, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import username from './Register'
const Styles = styled.div`
  a,
  .navbar-brand,
  .navbar-nav,
  nav-link {
    color: #adb1b8;
    &:hover {
      color: white;
    }
  }
`;

const NaviBar = () => {
  
  const[isAuth, setIsAuth] = useState(false)
  useEffect(() => {
    if (localStorage.getItem('access_token')!==null){
      setIsAuth(true)
    }
  },[isAuth])

  const [show, setShow] = useState(false);
  const [showSign, setShowSign] = useState(false);

  const [, setUserEmail] = useState(null);
  const [loginEmail, setLoginEmail] = useState(''); //      

  const [loginPassword, setLoginPassword] = useState(''); //      
  const [registerEmail, setRegisterEmail] = useState(''); //      
  const [registerPassword, setRegisterPassword] = useState(''); //      

  const handleClose = () => setShow(false);
  const handleCloseSign = () => setShowSign(false);

  const handleLogin = () => {
    
    setUserEmail(loginEmail); //     
    handleClose(); //   
  };

  const handleRegister = () => {
    
    setUserEmail(registerEmail); //     
    handleCloseSign(); //   
  };

  const [showBron, setShowBron] = useState(false);

  const handleCloseBron = () => setShowBron(false);
  const handleShowBron = () => setShowBron(true);



  return (
    <>
    <Styles>
    <Navbar collapseOnSelect expand='lg' bg='dark' variant='dark'>
  <Container>
    <Navbar.Brand>Block WW</Navbar.Brand>
    <Navbar.Toggle aria-controls='responsive-navbar-nav' />
    <Navbar.Collapse className='responsive-navbar-nav'>
      <Nav className='me-auto'>
        <Nav.Link as={Link} to='/' style={{ textDecoration: 'none' }}>
          
        </Nav.Link>
        <Nav.Link as={Link} to='/about' style={{ textDecoration: 'none' }}>
           
        </Nav.Link>
        <Nav.Link as={Link} to='/menu' style={{ textDecoration: 'none' }}>
          
        </Nav.Link>
      </Nav>
      <div className="bron-button-container">
        <Button onClick={() => handleShowBron(true)} className='button-bron' variant='primary'> </Button>
      </div>
      {isAuth ? (
        <>
          <span className='nav-link active'>, {username}!</span>
          <Link to="/logout" className='nav-link active'></Link>
        </>
      ) : (
        <>
          <Link to="/login" className='nav-link active' style={{ marginRight: '8px' }}></Link>
          <Link to="/register" className='nav-link active'></Link>
        </>
      )}
    </Navbar.Collapse>
  </Container>
</Navbar>
      </Styles>
      
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group controlId='formBasicEmail'>
              <Form.Label></Form.Label>
              <Form.Control 
                type='email' 
                placeholder=' ' 
                value={loginEmail}
                onChange={(e) => setLoginEmail(e.target.value)} //    
              />
            </Form.Group>
            <Form.Group controlId='formBasicPassword'>
              <Form.Label></Form.Label>
              <Form.Control type='password' 
                placeholder=' ' 
                value={loginPassword}
                onChange={(e) => setLoginPassword(e.target.value)} //    
              />
            </Form.Group>
            <Button variant='primary' style={{ marginTop: '8px' }} onClick={handleLogin}>
              
            </Button>
          </Form>
        </Modal.Body>
      </Modal>

      <Modal show={showSign} onHide={handleCloseSign}>
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group controlId='formBasicName'>
              <Form.Label></Form.Label>
              <Form.Control type='text' placeholder='  ' />
            </Form.Group>
            <Form.Group controlId='formBasicEmail'>
              <Form.Label></Form.Label>
              <Form.Control 
                type='email' 
                placeholder=' ' 
                value={registerEmail}
                onChange={(e) => setRegisterEmail(e.target.value)} //    
              />
            </Form.Group>
            <Form.Group controlId='formBasicPassword'>
              <Form.Label></Form.Label>
              <Form.Control 
                type='password' 
                placeholder=' ' 
                value={registerPassword}
                onChange={(e) => setRegisterPassword(e.target.value)} //    
              />
            </Form.Group>
            <Button variant='primary' style={{ marginTop: '8px' }} onClick={handleRegister}>
              
            </Button>
          </Form>
        </Modal.Body>
      </Modal>

      <Modal show={showBron} onHide={handleCloseBron}>
      <Modal.Header closeButton>
      <Modal.Title> </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId='fromBasicEmail'>
              <Form.Label> </Form.Label>
              <Form.Control type=' ' placeholder=' '/>
          </Form.Group>
          <Form.Group controlId='fromBasicPassword'>
              <Form.Label> </Form.Label>
              <Form.Control type=' ' placeholder='  '/>
          </Form.Group>
          <Form.Group controlId='fromBasicCheckbox'>
          </Form.Group>
          <Button variant='primary' style={{ marginTop: '8px' }} onClick={handleCloseBron}>
              
            </Button>
        </Form>
      </Modal.Body>
    </Modal>

    </>
  );
};

export default NaviBar;